/* source-sans-pro-300 - latin */
@font-face {
  font-display: swap;
  font-family: 'Source Sans Pro';
  font-style: normal;
  font-weight: 300;
  src: url('/static/assets/fonts/source-sans-pro-v21-latin-300.eot');
  src: url('/static/assets/fonts/source-sans-pro-v21-latin-300.eot?#iefix') format('embedded-opentype'),
       url('/static/assets/fonts/source-sans-pro-v21-latin-300.woff2') format('woff2'),
       url('/static/assets/fonts/source-sans-pro-v21-latin-300.woff') format('woff'),
       url('/static/assets/fonts/source-sans-pro-v21-latin-300.ttf') format('truetype'),
       url('/static/assets/fonts/source-sans-pro-v21-latin-300.svg#SourceSansPro') format('svg');
}
